.blob {
  position: relative;
  bottom: 167px;
  left: 136px;
	background: #ff4800;
	border-radius: 50%;
	margin: 10px;
	height: 15px;
	width: 15px;
  box-shadow: 0 0 0 0 rgb(109, 106, 106);
	transform: scale(1);
	animation: pulse 2s infinite;
}

@keyframes pulse {
	0% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(233, 106, 106, 0.7);
	}

	70% {
		transform: scale(1);
		box-shadow: 0 0 0 10px rgba(196, 52, 52, 0);
	}

	100% {
		transform: scale(0.95);
		box-shadow: 0 0 0 0 rgba(189, 50, 50, 0);
	}
}

