.goToTop::before,
.goToTop::after {
  display: inline-block;
  content: "";
  border-top: 1px solid rgba(255, 255, 255, 0.226);
  width: 500px;
  margin: 0 15px;
  transform: translateY(-1rem);
}
.goToTop i {
  position: relative;
  bottom:10px;
  transform: rotate(-90deg);
  box-shadow: 0 rgb(109, 106, 106);
  animation: MoveUpDown 1s linear infinite;
  color: #ff4800;
}


@keyframes MoveUpDown {
  0%, 100% {
    bottom: 10;
  }
  50% {
    bottom: 15px;
  }
}

